<template>
  <div class="json-text-block">
    <!-- name/value -->
    <!-- @click.stop  TextBlock -->
    <div class="prop" @click.stop="isFold = !isFold">
      <!-- 00-->
      <div v-if="deepth > 0 || properties.length > 0" class="indent" :style="indentStyle">
        <div class="triangle" :class="arrowClass"></div>
      </div>
      <!--  -->
      <template v-if="hasName">
        <span class="key public">{{name}}</span>
        <span class="space">: </span>
      </template>
      <!--  -->
      <JSONTextInlineBlock
        :name="name"
        :value="descriptor.value"
        class="nowrap"
      />
    </div>
    <!--  -->
    <!--  v-show  -->
    <div v-for="(item, index) in properties" :key="String(item.name) + index" v-show="!isFold">
      <JSONTextBlock
        :name="item.name"
        :descriptor="item.descriptor"
        :deepth="deepth + 1"
      />
    </div>
  </div>
</template>

<script>
/**
 *  JSON 
 *
 *  obj
 * obj = {
 *  a: 1,
 *  b: 2
 * }
 * UI 
 * {
 *  a: 1,
 *  b: 2
 * }
 *
 * 
 * // 
 * <text-block :descriptor="{value: 1}" />
 * // 
 * <text-block :name="age" :descriptor="{value: 1}" />
 */
import { isObject, flatMap, _console, isFunction } from "@/utils";
import JSONTextInlineBlock from "./JSONTextInlineBlock";

export default {
  name: "JSONTextBlock",
  components: {
    JSONTextInlineBlock
  },
  props: {
    //  Object.defineProperty() 
    descriptor: {
      type: Object,
      required: true,
      validator: descriptor => {
        try {
          Object.defineProperty({}, "key", descriptor);
        } catch (error) {
          _console.error(error.message, "descriptor:", descriptor);
        }
        // ConsolePanel 
        return true;
      }
    },
    // 
    name: [String, Symbol],
    // 
    // obj = {a: 1, b: {c: 2}}
    // obj  0
    // obj.b  1
    // obj.b.c  2
    deepth: {
      type: Number,
      default: 0
    },
    // 
    initialUnfold: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      // 
      isFold: true
    };
  },
  computed: {
    isRoot() {
      return !Boolean(this.name);
    },
    hasName() {
      return !!this.name;
    },
    // 
    properties() {
      // 
      //  value  getter 
      let value = this.descriptor.value;

      let obj;
      if (isObject(value)) {
        obj = value;
      } else {
        return [];
      }

      // 
      const ownKeys = Object.keys(obj);
      const properties = flatMap(ownKeys, name => {
        const descriptor = Object.getOwnPropertyDescriptor(obj, name);
        if (!descriptor) {
          return [];
        }

        // value and writable  get/set 
        if (isFunction(descriptor.get) && isFunction(descriptor.set)) {
          //  getter  setter
          return [
            { name, descriptor },
            {
              // Symbol 
              name: `get ${String(name)}`,
              descriptor: {
                value: descriptor.get,
                enumerable: false
              }
            },
            {
              name: `set ${String(name)}`,
              descriptor: {
                value: descriptor.set,
                enumerable: false
              }
            }
          ];
        } else if (isFunction(descriptor.get) && !isFunction(descriptor.set)) {
          //  getter
          return [
            { name, descriptor },
            {
              name: `get ${String(name)}`,
              descriptor: {
                value: descriptor.get,
                enumerable: false
              }
            }
          ];
        } else if (!isFunction(descriptor.get) && isFunction(descriptor.set)) {
          //  setter
          return [
            {
              name: `set ${String(name)}`,
              descriptor: {
                value: descriptor.set,
                enumerable: false
              }
            }
          ];
        } else {
          //  getter  setter value
          return [{ name, descriptor }];
        }
      });

      // 
      return properties.sort(propCompareFn);
    },
    indentStyle() {
      return {
        width: this.deepth > 0 ? `${this.deepth}em` : "auto"
      };
    },
    // /CSS
    arrowClass() {
      return this.properties.length > 0 ? (this.isFold ? "fold" : "unfold") : "";
    }
  },
  mounted() {
    this.isFold = !this.initialUnfold;
  },
  methods: {}
};

/**
 * 
 */
function propCompareFn(propA, propB) {
  const a = String(propA.name);
  const b = String(propB.name);
  return a < b ? -1 : a > b ? 1 : 0;
}
</script>

<style scoped lang="scss">
.json-text-block {
  display: flex;
  flex-direction: column;
  line-height: 1.4em;
  .indent {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    padding-right: 3px;
    flex-shrink: 0;
  }
  .triangle {
    width: 0;
    height: 0;
    $border-width: 5px;
    /* tan(30)  0.5773502691896257 */
    &.fold {
      border-left: $border-width solid #727272;
      border-top: $border-width * 0.8 solid transparent;
      border-bottom: $border-width * 0.8 solid transparent;
    }
    &.unfold {
      border-top: $border-width solid #727272;
      border-left: $border-width * 0.8 solid transparent;
      border-right: $border-width * 0.8 solid transparent;
    }
  }
  .content {
    display: flex;
    flex-direction: column;
  }
  .prop {
    display: flex;
    flex-direction: row;
    .key {
      flex-shrink: 0;
      &.public {
        color: rgb(136, 19, 145);
      }
    }
  }
  .space {
    white-space: pre;
    flex-shrink: 0;
  }

  .italic {
    font-style: italic;
  }
  .nowrap {
    white-space: nowrap;
  }
}
</style>
