<template>
  <!--  -->
  <span v-if="isFunction" class="text-inline-block function-type">
    <template v-if="deepth === 0">
      <span v-if="isArrowFunction">{{value}}</span>
      <span v-else>
        <span class="function"></span><span> {{value.name}}()</span>
      </span>
    </template>
    <template v-else>
      <span class="function"></span>
    </template>
  </span>
  <!--  -->
  <!--  -->
  <!-- [1, 2, 3]  deepth  0  -->
  <!-- Array(3)  deepth  0  showValueDetail  false {a: 1, b: Array(3)}, [1, Array(3)] -->
  <span v-else-if="isArray" class="text-inline-block array-type">
    <template v-if="showValueDetail">
      <template v-if="deepth === 0">
        <span style="color: gray">({{value.length}})&nbsp;</span>
        <span>[</span>
        <span v-for="(v, i) in value" :key="i">
          <text-inline-block
            :name="String(i)"
            :value="short(v)"
            :deepth="deepth + 1"
          />
          <span v-if="i !== value.length - 1">,&nbsp;</span>
        </span>
        <span>]</span>
      </template>
      <span v-else>Array({{value.length}})</span>
    </template>
    <span v-else>Array({{value.length}})</span>
  </span>
  <!--  -->
  <!-- -->
  <!-- '{a: 1, b: 2}'  deepth  0 -->
  <!-- '{...}'  deepth  0 {a: 1, b: {...}} -->
  <!-- 'Object'  showValueDetail  true {a: 1, __proto__: Object}  -->
  <span v-else-if="isObject" class="text-inline-block object-type">
    <!--  -->
    <template v-if="showValueDetail">
      <span>{</span>
      <!-- 1 5  5  -->
      <template v-if="deepth === 0">
        <span v-for="(propName, index) in displayPropertyNames" :key="index" v-if="index < maxDisplayPropertyCount">
          <span>{{propName}}: </span>
          <text-inline-block
            :name="propName"
            :value="short(value[propName])"
            :deepth="deepth + 1"
          />
          <span v-if="index !== Math.min(maxDisplayPropertyCount - 1, displayPropertyNames.length - 1)">, </span>
        </span>
        <span v-if="displayPropertyNames.length >= 5">, ...</span>
      </template>
      <!-- 1 -->
      <span v-else>...</span>
      <span>}</span>
    </template>
    <!--  -->
    <span v-else>Object</span>
  </span>
  <!--  -->
  <span v-else-if="isString" class="text-inline-block string-type">
    <template v-if="name">
      <span class="string-quote">"</span>
      <span class="string keep-format">{{value}}</span>
      <span class="string-quote">"</span>
    </template>
    <template v-else>
      <span class="keep-format">{{value}}</span>
    </template>
  </span>
  <!--  -->
  <span v-else class="text-inline-block" :class="valueClass">{{formattedValue}}</span>
</template>

<script>
/**
 * 
 *
 *  obj
 * obj = {
 *  a: 1,
 *  b: 2
 * }
 * UI 
 * {a: 1, b: 2}
 *
 *  chrome devtools 
 * 1 5 
 * obj = {
 *  a: 1,
 *  b: 2,
 *  c: 3,
 *  d: 4,
 *  e: 5,
 *  f: 6
 * }
 * UI 
 * {a: 1, b: 2, c: 3, d: 4, e: 5, ...}
 *
 * 2
 * obj = {
 *  // 
 *  a: 1,
 *  // 
 *  b: 1,
 *  // get 
 *  c: (...)
 * }
 * UI 
 * {a: 1, b: 1}
 *
 * 3 0   0 
 * obj = {
 *  a: () => {},
 *  b: function () {},
 *  c: function say () {}
 * }
 *  0 
 * {a: , b: , c: }
 *  0 
 * {
 *  a: () => {}
 *  b:  (),
 *  c:  say()
 * }
 */
import {
  isNull,
  isBoolean,
  isString,
  isNumber,
  isObject,
  isSymbol,
  isUndefined,
  flatMap,
  _console,
  isArray,
  isFunction
} from "@/utils";

export default {
  name: "text-inline-block",
  props: {
    // 
    //  value  <TextInlineBlock/>
    //  value  {key: <TextInlineBlock/>}
    name: [String, Symbol],
    // 
    value: {},
    // 
    // let obj = {a: {b: 2}}
    // obj  deepth  0
    // obj.a  deepth  1
    // obj.a.b  deepth  2 
    deepth: {
      type: Number,
      default: 0
    },
    // 
    //  true  {a: 1}, [1, 2] 
    //  false  Object, Array 
    showValueDetail: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    isFunction() {
      return isFunction(this.value);
    },
    isArrowFunction() {
      return isFunction(this.value) && String(this.value).indexOf("() =>") === 0;
    },
    isArray() {
      return isArray(this.value);
    },
    isString() {
      return isString(this.value);
    },
    isObject() {
      return isObject(this.value);
    },
    maxDisplayPropertyCount() {
      return 5;
    },
    displayPropertyNames() {
      const obj = this.value;
      return (
        Object.getOwnPropertyNames(obj)
          // 
          .filter(name => {
            const descriptor = Object.getOwnPropertyDescriptor(obj, name);
            return "value" in descriptor;
          })
      );
    },
    formattedValue() {
      const value = this.value;
      if (isNull(value) || isUndefined(value)) {
        return String(value);
      }
      return value;
    },
    valueClass() {
      const value = this.value;
      return {
        null: isNull(value),
        undefined: isUndefined(value),
        boolean: isBoolean(value),
        number: isNumber(value),
        symbol: isSymbol(value)
      };
    }
  },
  methods: {
    short(value) {
      const maxLen = 40;
      if (isString(value) && value.length > maxLen) {
        return value.slice(0, maxLen / 2) + "..." + value.slice(value.length - maxLen / 2);
      }

      return value;
    }
  }
};
</script>

<style lang="scss" scoped>
.normal {
  color: #565656;
}
.null,
.undefined {
  color: rgb(128, 128, 128);
}
.boolean,
.function {
  color: rgb(13, 34, 170);
}
.number {
  color: #1c00cf;
}
.string,
.symbol {
  color: rgb(196, 26, 22);
}
.string-quote {
  color: #222;
}
.keep-format {
  white-space: pre-wrap;
}
</style>
